import html2Canvas from "html2canvas";
import JsPDF from "jspdf";

export default async function exportPdf(title: string, id: string) {
    return new Promise((resolve, reject) => {
        const dom = document.querySelector(id) as HTMLElement;
        if (!dom) {
            return;
        }

        html2Canvas(dom).then((canvas) => {
            const pdf = new JsPDF("p", "mm", "a4");
            const ctx = canvas.getContext("2d");
            // a4
            const a4w = 190;
            const a4h = 272;
            // è®¡ç®—ä¸€é¡µé«˜åº¦
            const imgHeight = Math.floor((a4h * canvas.width) / a4w);
            if (!ctx) return;
            let renderedHeight = 0;

            while (renderedHeight < canvas.height) {
                const page = document.createElement("canvas");
                page.width = canvas.width;
                page.height = Math.min(
                    imgHeight,
                    canvas.height - renderedHeight
                ); //å¯èƒ½å†…å®¹ä¸è¶³ä¸€é¡µ

                //ç”¨getImageDataå‰ªè£æŒ‡å®šåŒºåŸŸï¼Œå¹¶ç”»åˆ°å‰é¢åˆ›å»ºçš„canvaså¯¹è±¡ä¸­
                const context = page.getContext("2d");
                if (!context) return;

                context.putImageData(
                    ctx.getImageData(
                        0,
                        renderedHeight,
                        canvas.width,
                        Math.min(imgHeight, canvas.height - renderedHeight)
                    ),
                    0,
                    0
                );
                pdf.addImage(
                    page.toDataURL("image/jpeg", 1.0),
                    "JPEG",
                    10,
                    10,
                    a4w,
                    Math.min(a4h, (a4w * page.height) / page.width)
                ); //æ·»åŠ å›¾åƒåˆ°é¡µé¢ï¼Œä¿ç•™10mmè¾¹è·

                renderedHeight += imgHeight;
                if (renderedHeight < canvas.height) {
                    pdf.addPage(); //å¦‚æœåé¢è¿˜æœ‰å†…å®¹ï¼Œæ·»åŠ ä¸€ä¸ªç©ºé¡µ
                }
            }

            pdf.save(`${title}.pdf`);
            resolve(null);
        });
    });
}
